<template>
  <div
    v-if="currentInfo"
    id="scroll"
  >
    <van-cell-group style="margin-top:10px;">
      <van-cell
        title="收款状态"
        :value-class="'color'+currentInfo.status"
        :value="currentInfo.status == '1'?'已确认':'待确认'"
      />
      <van-cell
        title="确认人"
        v-if="currentInfo.status == '1'"
        value-class='c_00'
        :value="currentInfo.companyName || '暂无'"
      />
      <van-cell
        title="确认时间"
        v-if="currentInfo.status == '1'"
        value-class='c_00'
        :value="currentInfo.payeeTime || '暂无'"
      />
    </van-cell-group>
    <div class="info">付款信息</div>
    <van-cell-group>
      <van-cell
        title="付款名称"
        value-class='c_00'
        :value="currentInfo.payer || '暂无'"
      />
      <van-cell
        title="付款金额"
        value-class="color0"
        :value="realAmount"
      />
      <van-cell title="付款凭证">
        <template>
          <img
            :src="currentInfo.payImage"
            style="width:50px;height:50px;"
          >
        </template>
      </van-cell>
    </van-cell-group>
    <div class="info">收款信息</div>
    <van-cell-group>
      <van-cell
        title="公司名称"
        value-class='c_00'
        :value="currentInfo.companyName || '暂无'"
      />
      <van-cell
        title="付款账号"
        value-class='c_00'
        :value="currentInfo.companyBankAccount || '暂无'"
      />
      <van-cell
        title="支行名称"
        value-class='c_00'
        style="margin-bottom:60px;"
        :value="currentInfo.companyAccountName || '暂无'"
      />
    </van-cell-group>
    <van-button
      v-if="currentInfo.status == '0'"
      class="ok"
      @click="configPayed"
    >确认到款</van-button>
  </div>
</template>
<script>
import { GetBusinessFinancialRecord, PostBusinessFinancialRecordConfirm } from '@/api/api_business_bsm';
import { Dialog, Toast } from 'vant';
export default {
    data() {
        return {
            currentInfo: null,
            realAmount: null
        };
    },
    created() {
        this.$store.commit('setHeader', '详情信息');
        this.getInfo();
    },
    methods: {
        getInfo() {
            GetBusinessFinancialRecord({ financialRecordId: this.$route.query.id }).done((res) => {
                this.currentInfo = res.data;
                this.realAmount = this.currentInfo.realAmount ? '¥' + Number(this.currentInfo.realAmount).toFixed(2) : '暂无';
            });
        },
        configPayed() {
            Dialog.confirm({
                title: '确认到账?'
            })
                .then(() => {
                    PostBusinessFinancialRecordConfirm({ financialRecordId: this.$route.query.id }).done((res) => {
                        Toast({
                            message: res.msg,
                            duration: 900
                        });
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 900);
                    });
                })
                .catch(() => {});
        }
    }
};
</script>
<style scoped>
#scroll {
    overflow: auto;
    position: fixed;
    bottom: 0;
    top: 55px;
    right: 0;
    left: 0;
}
.info {
    font-size: 0.8rem;
    text-align: left;
    padding: 10px 16px;
    padding: 0 16px;
    margin: 20px 0 10px 0;
}
.ok {
    width: 100%;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: #ff7d41;
    color: #fff;
    border-radius: 5px;
}
.color0 {
    color: #ff813e;
}
.color1 {
    color: #04bb70;
}
.c_00 {
    color: #000;
}
.van-cell__title {
    text-align: left;
}
</style>



